import axios from 'axios';

export default class NetworksApi{

    static list(token, params){
        return axios({
            method: "GET",
            headers: {"Authorization": `Bearer ${token}`},
            params,
            url: "/api/browser/settings/networks"
        });
    }

    static add(token, data){
        return axios({
            method: "POST",
            headers: {"Authorization": `Bearer ${token}`},
            data,
            url: "/api/browser/settings/networks"
        });
    }

    static edit(token, network, data){
        return axios({
            method: "PUT",
            headers: {"Authorization": `Bearer ${token}`},
            data,
            url: `/api/browser/settings/networks/${network}`
        });
    }

    static delete(token, network){
        return axios({
            method: "DELETE",
            headers: {"Authorization": `Bearer ${token}`},
            url: `/api/browser/settings/networks/${network}`
        });
    }
}


