import * as actionTypes from '../helpers/constants';
import {getTime} from "date-fns";
import NetworksApi from '../api/actual/Networks';


export const loadNetworks = (token, params = {}) => dispatch => {
    dispatch({type: actionTypes.LOAD_NETWORKS});

    return NetworksApi
        .list(token, params)
        .then(({data: networks}) => {
            dispatch({type: actionTypes.LOAD_NETWORKS_SUCCESSFUL, networks});
        })
        .catch(error => {
            dispatch({type: actionTypes.LOAD_NETWORKS_FAILED});
            console.log(error);});
};

export const addNetwork = (token, data) => dispatch => {
    dispatch({type: actionTypes.ADD_NETWORK});

    return NetworksApi
        .add(token, data)
        .then(({data: {created}}) => {
            if(created){
                dispatch({type: actionTypes.ADD_NETWORK_SUCCESSFUL});
                dispatch({
                    type: actionTypes.OPERATION_SUCCESSFUL,
                    action: actionTypes.ADD_NETWORK,
                    timestamp: getTime(Date()),
                    data: {created}
                });
            }
        })
        .catch(error => {
            dispatch({
                type: actionTypes.OPERATION_FAILED,
                action: actionTypes.ADD_NETWORK,
                timestamp: getTime(Date()),
                data: {...error}
            });
            dispatch({type: actionTypes.ADD_NETWORK_FAILED});
            console.log(error);});
};

export const editNetwork = (token, network, data) => dispatch => {
    dispatch({type: actionTypes.EDIT_NETWORK});

    return NetworksApi
        .edit(token, network, data)
        .then(({data: {updated}}) => {
            if(updated){
                dispatch({type: actionTypes.EDIT_NETWORK_SUCCESSFUL});

                dispatch({
                    type: actionTypes.OPERATION_SUCCESSFUL,
                    action: actionTypes.EDIT_NETWORK,
                    timestamp: getTime(Date()),
                    data: {updated}
                });
            }
        })
        .catch(error => {
            dispatch({
                type: actionTypes.OPERATION_FAILED,
                action: actionTypes.EDIT_NETWORK,
                timestamp: getTime(Date()),
                data: {...error}
            });
            dispatch({type: actionTypes.EDIT_NETWORK_FAILED});
            console.log(error);});
};

export const deleteNetwork = (token, network) => dispatch => {
    dispatch({type: actionTypes.DELETE_NETWORK});

    return NetworksApi
        .delete(token, network)
        .then(({data: {deleted}}) => {
            if(deleted){
                dispatch({type: actionTypes.DELETE_NETWORK_SUCCESSFUL});
                dispatch({
                    type: actionTypes.OPERATION_SUCCESSFUL,
                    action: actionTypes.DELETE_NETWORK,
                    timestamp: getTime(Date()),
                    data: {deleted}
                });
            }
        })
        .catch(error => {
            dispatch({
                type: actionTypes.OPERATION_FAILED,
                action: actionTypes.DELETE_NETWORK,
                timestamp: getTime(Date()),
                data: {...error}
            });
            dispatch({type: actionTypes.DELETE_NETWORK_FAILED});
            console.log(error);});
};


